import React, { useCallback, useState } from 'react';
import { Header } from 'components/atoms';
import classNames from 'classnames';

interface MenuTopProps {
  logo?: React.ReactNode;
  children?: React.ReactNode | React.ReactNode[];
}

const MenuTop = ({ logo, children }: MenuTopProps) => {
  const [isVisible, setIsVisible] = useState(false);
  const toggleVisible = useCallback(() => {
    setIsVisible((visible) => !visible);
  }, []);

  return (
    <Header>
      <a href="https://flowbite.com/" className="flex items-center">
        {logo}
      </a>
      <div className="flex md:order-2">
        <button
          data-collapse-toggle="navbar-sticky"
          type="button"
          className="inline-flex items-center p-2 text-sm text-gray-500 rounded-lg md:hidden hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-200 dark:text-gray-400 dark:hover:bg-gray-700 dark:focus:ring-gray-600"
          aria-controls="navbar-sticky"
          aria-expanded="false"
          onClick={toggleVisible}
        >
          <span className="sr-only">Open main menu</span>
          <svg
            className="w-6 h-6"
            aria-hidden="true"
            fill="currentColor"
            viewBox="0 0 20 20"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fillRule="evenodd"
              d="M3 5a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 10a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 15a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1z"
              clipRule="evenodd"
            />
          </svg>
        </button>
      </div>
      <div
        className={`${classNames({ hidden: !isVisible })} justify-between items-center w-full md:flex md:w-auto md:order-1`}
        id="navbar-sticky"
      >
        <ul className="flex flex-col w-full p-4 mt-4 rounded-lg md:flex-row md:space-x-8 md:mt-0 md:font-medium md:border-0 h-full">
          {children}
        </ul>
      </div>
    </Header>
  );
};

export { MenuTop };
